.product {
  margin-top: -15px;
  background-image: url(../../../assets/img/productimg.png);
  background-size: contain;
  background-repeat: repeat;
  padding-bottom: 208px;
}

.container {
  margin: 66px 0;
  max-width: 1220px;
  width: 100%;
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.container>* {
  margin-bottom: 107px;
}

.title {
  margin: 51px 0 107px 0;
  font-style: normal;
  font-weight: 600;
  font-size: 64px;
  text-shadow: 1px 1px 2px black;
  text-align: center;
  color: #ffffff;
}

.table_text {
  border-bottom: 1px solid #e7eaf1;
}

.selectionsKey {
  text-align: center;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 8px 20px;
  color: white;
  width: 99px;
  height: 48px;
  background: #d31b1b;
  border-radius: 100px;
  margin: 0 20px;
  transition: all 0.3s;
  cursor: pointer;
}

.selectionsKey:hover {
  color: #d31b1b;
  background-color: white;
}

.selectionkeys {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
  flex-wrap: wrap;
}

.statebar {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  gap: 10px;
}

.feature h2 {
  color: white;
}

.products_list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 97px 32px;
  padding: 30px;
  /* width: 95%; */
  /* flex-wrap: wrap;
      justify-content: center;
      align-items: center;
      gap: 32px;
      grid-row-gap: 97px; */
}

.products_list>div {
  background-color: white;
  box-shadow: 0px 9px 20px rgba(46, 35, 94, 0.07) !important;
}

@media screen and (max-width: 1024px) {
  .products_list {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media screen and (max-width: 768px) {
  .products_list {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (max-width: 450px) {
  .products_list {
    grid-template-columns: repeat(1, 1fr);
  }
}

.advice {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  color: #e7eaf1;
  gap: 32px;
}

.part {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 291px;
  max-width: 379px;
  background: linear-gradient(180deg,
      #000000 0%,
      rgba(156, 156, 156, 0.677151) 50.52%,
      rgba(12, 12, 12, 0) 100%);
  border: 1px solid rgba(169, 164, 164, 0.4);
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  border-radius: 12px;
  padding: 20px;
  cursor: pointer;
  transition: all 0.3s;
}

.part:hover {
  transform: scale(1.03);
}

.content {
  display: flex;
  /* flex-direction: column; */
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.content>div {
  flex: 1 1 265px;
}

.addTitle {
  font-size: 20px;
  color: white;
  cursor: pointer;
}

.content h3 {
  font-size: 24px;
  line-height: 150%;
  letter-spacing: -0.5px;
  margin-bottom: 16px;
  color: #e7eaf1;
}

.content p {
  font-weight: 400;
  font-size: 14px;
  letter-spacing: -0.1px;
  line-height: 150%;
  /* or 21px */
  text-align: center;
  color: #dee0e6;
}

.icon {
  width: 56px;
  height: 56px;
}

.pagenation {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
  text-align: center;
  border-top: 0.2px solid grey;
  padding-top: 24px;
}

.pagenation div:nth-child(1) {
  display: flex;
  align-items: center;
  color: white;
  justify-content: start;
}

.pagenation div:nth-child(2) {
  align-items: center;
  display: flex;
  color: white;
  justify-content: end;
}

.pagenation div:nth-child(2) * {
  color: white;
}

.Title {
  padding: 15px 60px;
  margin: 0 0 28px 0;
  background-color: #fff;
  color: #353434;
  font-size: 24px;
  font-weight: 600 !important;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.addItem {
  width: 100%;
  background-color: #f0f0f0;
  min-height: 90vh;
}

.addItem .style_img_part__3MR09:hover .style_img_hover__aQ0b5 {
  display: none !important;
}

.table_area {
  width: 95%;
  margin: auto;
  margin-top: 40px;
  background-color: white;
  padding: 40px;
  border-radius: 10px;
}