.delete {
  background-color: #fc4438;
  padding: 10px;
  color: white;
}

.edit {
  background-color: #54ba4a;
  color: white;
  padding: 10px;
}

.admin_header_ui {
  display: flex;
  list-style-type: none;
  padding-right: 50px;
  align-items: center;
}

.admin_header_ui>li {
  padding-left: 35px;
  display: flex;
  align-items: center;
}

.view_product_input {
  margin-bottom: 15px;
  border: none;
  box-shadow: 1px 5px 24px 0 rgba(68, 102, 242, 0.05);
  height: 50px;
  padding-left: 30px;
  outline: none;
  width: 74%;
}

.header_list {
  padding: 0 30px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.header_list select {
  margin-bottom: 15px;
  border: none;
  box-shadow: 1px 5px 24px 0 rgba(68, 102, 242, 0.05);
  height: 50px;
  padding: 0 30px !important;
  outline: none;
  width: 23.5%;
  box-shadow: 0px 9px 20px rgba(46, 35, 94, 0.07);
}

.history_panel {
  border: none;
  border-radius: 15px;
  box-shadow: 0 9px 20px rgba(46, 35, 94, .07);
  letter-spacing: .5px;
  background-color: white;
  margin: 30px;
}

.history_panel .title {
  padding: 30px 30px;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.2;
  border: 0.5px solid #f1f0ff;
}

.history_panel .product_area {
  padding: 30px;
}

.history_panel .list {
  display: flex;
  width: fit-content;
  align-items: center;
  border-radius: 15px;
  border: 0.5px solid #cecece;

}

.history_panel .list>div {
  padding: 18px;
  display: flex;
  flex-direction: column;
}

.history_panel .list>div p {
  margin: 0 !important;
}

.history_panel .list>div>* {
  padding: 5px 0;
}

.history_panel img {
  width: 70px;
  height: 70px;
  object-fit: cover;
}

.history_panel .process {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.history_panel .process span {
  color: #fff;
  border-color: #0a58ca;
  padding: 4px;
  border-radius: 5px;
  margin-left: 250px;
}

.history_panel .process p {
  color: #198754;
}

.payment_area {
  padding: 30px;
  display: flex;
}

.credit_card, .debit_card {
  background-color: white;
  flex: 0 0 auto;
  width: 66.66666667%;
  margin-bottom: 30px;
  margin-right: 30px;
  border: none;
  transition: all 0.3s ease;
  letter-spacing: 0.5px;
  border-radius: 15px;
  box-shadow: 0px 9px 20px rgba(46, 35, 94, 0.07);
