  padding: 30px 0 30px 30px;
}

.debit_list {
  padding: 30px;
  display: flex;
  flex-direction: column;
}

.credit_list > div, .credit_list > div > * {
  margin:0 20px 30px 0 ;
}

.debit_list > div {
  margin-bottom: 30px;
  display: flex;
}

.debit_list > div > * {
