}

.debit_card {
  width: 31%;
}

.payment_area .title {
  padding: 30px 30px;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.2;
  border: 0.5px solid #f1f0ff;
}

.myItem_list * {
