.footer {
    position: relative;
    display: flex;
    margin: auto;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 344px 0 64px;  
    color: white;
    width: 100%;
    background: linear-gradient(180deg, #1A1E26 0%, #01100B 100%);
}

.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    grid: 64px;
    max-width: 1200px;
    width: 100%;
}

.part1 {
    display: flex;
    width: 100%;
   
    flex-wrap: wrap;
}

.quick {
    max-width: 378px;
    width: 100%;
   
    display: flex;
    flex-direction: column;
    /* justify-content: space-around; */
}

.footer h5 {
    margin-bottom: 24px;
    font-weight: 500;
    font-size: 20px;
    line-height: 150%;
    /* identical to box height, or 30px */


    color: #FFFFFF;
}

.footer p {
    margin-bottom: 14px;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    /* identical to box height, or 21px */


    /* Neutral/200 */

    color: #9D9EA2;
}

.contact {
    max-width: 378px;
    
    display: flex;
    flex-direction: column;
}



.more {
    display: flex;
    flex-direction: column;
    margin-top: 40px;   
}

.more h5 {
    font-weight: 500;
    font-size: 20px;
    line-height: 150%;
    /* identical to box height, or 30px */
    color: #FFFFFF;
}

.more_content {
    display: flex;
    padding: 0px;
    flex-wrap: wrap;
}

.bottom {
    margin-top: 64px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 40px 0px 0px;
    max-width: 1200px;
    flex-wrap: wrap;
    height: 64px;
    /* Neutral/400 */
    border-top: 1px solid #46494F;       
}

.b_right {
    display: flex;
    gap: 32px;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: #9D9EA2;
}

.b_left {
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    /* identical to box height, or 24px */
    /* Neutral/300 */
    color: #717378;
}

.left {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 378px;
}

.right {
    width: 378px;
}

.left p{
    margin-bottom: 16px;
}

.right p{
    margin-bottom: 16px;
    
}

.visa {
    display: flex;
    flex-wrap: wrap;
    gap: 16px;
    margin-top: 40px;
}

.visa div{
    width: 56px;
    height: 32px;
    background: #FFFFFF;
    border-radius: 6px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: all 0.2s;
}

.visa div:hover {
    background-color: rgb(34, 32, 32);
}

.footer p {
    cursor: pointer;
}

.submit {
    position: absolute;
    max-width: 1200px;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    /* align-items: center; */
    justify-content: space-around;
    padding: 64px;
    gap: 16px;
    top: -188px;
    background: #3F3E3E;
    border-radius: 24px;
}

.textfield {
    display: flex;
    justify-content: space-between;
    width: 100%;
    flex-wrap: wrap;
    gap: 40px;
    padding-top: 32px;
    border-top: 1px solid #6A5555;
}

.input {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 24px 44px;
    gap: 8px;
    width: 70%;
    height: 72px;
    background: #FFFFFF;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 100px;
    border: none;
    outline: none;
}

.button {
    display: flex !important;
    align-items: center !important;
    flex-direction: row !important;
    padding: 24px 44px !important;
    gap: 8px !important;

    height: 72px !important;
    width: 205px !important;
    
    background: rgba(255, 255, 255, 0.23) !important;
    box-shadow: 0px 4px 3px rgba(0, 0, 0, 0.25) !important;
    border-radius: 100px !important;
}

.button::placeholder {
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: #939695;
}

.text {
    display: flex;
    flex-direction: column;
    gap: 24px;
}