.card {
    /* max-width: 276px; */
    width: 100%;
    border: 1px solid #FFFAFA;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    border-radius: 8px;
    text-align: center;
    transition: all 0.3s;
}

.card:hover {
    box-shadow: 1px 0 10px white;
    transform: scale(1.005);
}

.img {
    width: 100%;
    /* height: 186px; */
    border-radius: 8px;
    position: relative;
}

.img_part:hover div{
    display: flex;
}

.context {
    padding: 28px 5px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.context > * {
    margin: 3px;
}

.subtitle {
    font-weight: 300;
    font-size: 14px;
    line-height: 150%;
    /* identical to box height, or 21px */

    text-align: center;

    /* Neutral/200 */

    color: #9D9EA2;
}

.label, .label2 {
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;
    /* or 27px */
    text-align: center;
    /* color: #FFFFFF; */
}

.review_list {
    display: flex;
    color: #C8C9CB;
    align-items: center;
    justify-content: center;
}

.starreview {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding-right: 6px;
    border-right: 1px solid white;
}

.scorebutton {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding-left: 6px;
}

.percent {
    font-weight: 400;
    font-size: 12px;
    line-height: 150%;
    color: #02FFA4;
}

.money {
    display: flex;
    justify-content: center;
    align-items: center;
}

.old_price {
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    text-decoration-line: line-through;
    color: #9D9EA2;
    padding-right: 16px;
}

.new_price {
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: #EB2606;
}

.wigdet {
    display: flex;
    justify-content: center;
    align-items: center;
}

.wigdet p {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5px 10px;
    margin-left: 8px;
    gap: 12px;
    font-size: 12px;
    width: 42px;
    height: 32px;
    background-color: #FFFFFF;
    /* Neutral/50 */
    border: 1px solid #F4F4F4;
    border-radius: 4px;
}

.button {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 0px 24px;
    gap: 8px;
    width: 128px;
    height: 40px;
    font-size: 14px;
    line-height: 150%;
    color: #FFFDFD;
    background: #EA5C5C;
    border-radius: 100px;
    transition: all 0.2s;
    cursor: pointer;
}

.button:hover {
    color: #EA5C5C;
    background: white;
}

.img_hover {
    display: none;
    position: absolute;
    width: 152px;
    height: 64px;
    left: calc(50% - 152px/2);
    top: calc(20% - 76px/2);
    background: rgba(26, 30, 38, 0.3);
    backdrop-filter: blur(4px);
    /* Note: backdrop-filter has minimal browser support */
    color: rgb(231, 229, 229);
    /* display: flex; */
    align-items: center;
    justify-content: center;
    border-radius: 100px;
    cursor: pointer;
}

