.img {
    width: 100%;
    height: 75vh;
    object-fit: cover;
}

.slide_problem {
    margin-top: -10px;
    background-image: url(../../../assets/img/backimg.jpg);
    background-size: cover;
    width: 100%;
    height: 350px;
}

.slide_part {
    display: flex;
    align-items: start;
    justify-content: space-around;
    cursor: pointer;

}

.slide_part > div {
    margin:10px 20px 0 0;
    text-align: start;
}

.icon {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}

.icon svg {
    color: black;
    width: 56px;
    height: 56px;
}

.slide_part div:nth-child(2) h2{
    font-weight: 500;
    font-size: 24px;
    /* or 36px */

    letter-spacing: -0.5px;

    color: #FFFFFF;
    margin-bottom: 16px;
}

.slide_part div:nth-child(2) p {
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    /* or 24px */


    color: #C1C3C7;
}

.img_part {
    width: 100% !important;
    height: 75vh !important;
    object-fit: cover !important;
    padding: 0 !important;
}

.img_part *{
    color: rgba(128, 128, 128, 0.507) !important;
    transition: all 0.2s;
}

.img_part *:hover {
    color: white !important;
}


.landing_img {
    animation-name: preload;
    animation-duration: 0.3s;
    animation-timing-function: ease-in;
    
}

@keyframes preload {
    from {
        opacity: 0.4s;
        margin-top: -100px;
        opacity: 0;
    }
    to {
        opacity: 1;
        margin-top: 0px;
    }
}