
.product {
    margin-top: -7px;
    background-image: url(../../../assets/img/productimg.png);
    background-size: cover;
    background-repeat: no-repeat;
    padding-bottom: 208px;
}

.container {
    margin: 66px 0;
    max-width: 1660px;
    width: 100%;
    padding: 81px 0;
    margin: auto;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}


.products_list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 32px;
    grid-row-gap: 97px;
}

.product h2 {
    font-size: 40px;
    color: white;
    padding:0 0 0 74px;
    font-weight: 700;
    font-size: 24px;
    line-height: 150%;
    /* identical to box height, or 36px */

    letter-spacing: -0.5px;

    color: #FFFFFF;
}

.products {
    display: flex;
    flex-direction: column;
    align-items: start;
    gap: 40px;
    justify-content: center
}

.category h2 {
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
    color: #FFFFFF;
}

.category {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0px 32px 0px 0px;
    gap: 20px;
    width: 304px;
    height: 1763px;

    /* Neutral/50 */

    border-right: 1px solid #F4F4F4;
}

